}

#navigationDrawer.open {
  transform: translateY(-70px);
}

#navigationDrawer ul {
  list-style-type: none;
  padding: 0; /* Remove default padding */
  margin: 0; /* Remove default margin */
}

#navigationDrawer a {
  margin-top: 10px;
  font-family: "Quicksand", serif;
  text-decoration: none;
  font-size: 30px;
  font-weight: bolder;
  color: #000000;
  transition: ease-in;
  transition-duration: 300ms;
  border-bottom: 1px solid #ffcb0e00;
  display: block; /* Ensure each link takes full width */
  align-items: center;
  padding: 10px; /* Add padding for better touch/click experience */
}

#navigationDrawer a:hover {
  color: #fff5e0;
}

#hamburgerButton {
  font-size: 32px;
  color: white;
  border: none;
  cursor: pointer;
  background-color: transparent;
}

#hamburgerButton:hover {
  color: #000000;
}

.content {
  flex: 1;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.blog {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 150px;
  gap: 2%;
  background-color: #ef9595;
}

.blog > div {
  width: 100%;
  font-family: "Rubik", sans-serif;
  font-size: 23px;
  color: #000000;
}

.blog > img {
  object-fit: cover;
  width: 75%;
  height: 150px;
  background-color: #ed7b7b;
}
.blog p {
  width: 100%;
  margin: 0 auto;
  font-size: 14px;
  line-height: 1.5;
}
.blogs {
  display: inline;
  justify-content: center;
  align-items: center;

  gap: 2%;
}

.blogs > div {
  width: 100%;
  font-family: "Rubik", sans-serif;
  font-size: 23px;
  color: #000000;
}

.blogs > img {
  object-fit: cover;
  width: 100%;
}

.blogs p {
  width: 100%;
  margin: 0 auto;
  padding: 20px;
  font-size: 18px;
  line-height: 1;
  text-align: center;
  background-color: #ef9595;
}

.title {
  font-family: "Poppins", sans-serif;
  font-size: xx-large;
  padding-top: 40px;
  font-weight: bold;
}

.slide-container {
  width: 100%;
  height: 100%;
  /* padding-bottom: 35px; */
}

.card-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.slide-content {
  padding: 20px;
  width: 90%;
  justify-content: center;
  margin: 0 auto;
}

.image-content-recipe,
.card-content-recipe {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1px 14px;
}

.card {
  border-radius: 0.85rem;
  background-color: #fff5e0;
}

.card-recipe {
  flex: 0 0 calc((100% - 3 * 15px) / 3);
  border-radius: 25px;
  background-color: #fff5e0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 25px 25px 0 25px;
}

.image-content {
  row-gap: 5px;
  padding: 25px 0;
}

.overlay {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: #ed7b7b;
  border-radius: 25px 25px 0 25px;
}

.card-image {
  height: 150px;
  width: 150px;
  border-radius: 50%;
  background: #fff5e0;
  padding: 3px;
}

.card-image .card-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.name a {
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  text-decoration: none;
}

.description {
  font-family: "Rubik", sans-serif;
  font-size: 14px;
  color: #000000;
  text-align: center;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.button {
  border: none;
  font-size: 16px;
  padding: 8px 16px;

  background-color: #ed7b7b;
  border-radius: 6px;
  margin: 14px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: "Rubik", sans-serif;
}

.button:hover {
  background: #ed7b7b;
}

.button a {
  color: #000000;
  text-decoration: none;
}

.button a:hover {
  color: #fff5e0;
}
