.result {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-rows: auto auto auto;
  gap: 18px 16px;
  padding: 32px;
}

.result .result-name {
  grid-column: 1 / span 2;
  font-weight: bold;
  margin: 0;
}

.result-picture {
  height: 100%;
  width: 85%;
  grid-column: 1;
  border-radius: 8px;
}

.result-description h3 {
  margin: 0;
  grid-column: 2;
}

.result-description p {
  text-align: justify;
}

.result-info h3 {
  margin: 8px 0;
}

.ingredients-list {
  list-style-type: none;
  padding: 0;
}

.result-steps {
  padding-left: 20px;
  text-align: justify;
}

.ingredients-and-picture {
  display: grid;
  grid-template-columns: 1fr; /* Kolom tunggal pada layar kecil */
  gap: 16px;
}

.ingredients,
.result-picture {
  width: 100%;
}

.ingredients-list li,
.result-steps li {
  margin-bottom: 5px; /* Jarak antara setiap bahan atau langkah */
}

/* recipes-page */
.contentDetail {
  margin: 0 auto;
  min-height: 100%;
}

.islands {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  margin: 32px;
  justify-content: c;
}

.region {
  display: grid;
  grid-template-columns: auto;
  margin: 32px 100px;
  gap: 40px;
  padding-left: 150px;
  padding-right: 150px;
}

/* region item template */
.result-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 18px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #ed7b7b; */
}

.result-item-region {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 18px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* background-color: #ed7b7b; */
}

.result-item-header {
  position: relative;
}

.result-item-picture {
  padding-top: 30px;
  width: 60%;
  object-fit: cover;
  border-radius: 18px;
}

.result-item-picture-islands {
  padding-top: 30px;
  width: 60%;
  object-fit: cover;
  border-radius: 48px/16px;
}

.result-item-picture-region {
  padding-top: 30px;
  width: 30%;
  object-fit: cover;
  border-radius: 50%;
}

.result-item .result-content {
  padding: 16px;
  text-align: center;
}

.result-item-region .result-content-region {
  padding: 16px;
  padding-left: 32px;
  padding-right: 32px;
  text-align: center;
}

.result-item .result-content h2 {
  margin: 0 0 10px 0;
  align-items: center;
}

.result-item .result-content h2 a {
  color: #000000;
  text-decoration: none;
}

.result-item .result-content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.result-item .result-content .button {
  margin-top: 10px; /* Mengatur jarak antara teks dan tombol */
}

