.footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px 5%;
  background-color: #ef9595;
  font-family: "Rubik", sans-serif;
  font-weight: bold;
  font-size: 12px;
}

.contact {
  display: block;
}

.contact h2 {
  font-size: 14px;
  font-family: "Rubik", sans-serif;
  font-weight: bold;
}

.contact > p {
  display: flex;
  gap: 10px;
}

.contact > p > img {
  width: 10%;
  font-weight: bold;
}

/* Style untuk profil tim */
.about-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.team {
  margin-top: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 15px;
  text-align: center;
}

.members {
  display: grid;
  grid-template-columns: repeat(5, 1fr); /* Tiga anggota per baris */
  grid-gap: 20px;
  padding: 20px;
}

.member {
  border-radius: 8px;
  border: 2px solid black;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  padding: 20px;
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease-in-out;
}

.member:hover {
  background-color: #ef9595;
}

.profile-pic {
  width: 100px; /* Sesuaikan ukuran foto profil */
  height: 100px; /* Sesuaikan ukuran foto profil */
  border-radius: 50%;
  object-fit: cover;
  object-position: center;
  margin-bottom: 10px;
}

.member h3 {
  margin-top: 0;
  font-size: 18px;
  text-align: center;
}

.member ul {
  padding: 0;
  list-style: none;
}

.member ul li {
  margin-bottom: 8px;
}

.member ul li a {
  text-decoration: none;
  color: #333;
}

.member ul li a:hover {
  color: rgb(0, 0, 0);
  font-weight: bold;
}

.member:hover {
  transform: scale(1.1);
}

.member .profile-pic {
  transition: transform 0.3s ease-in-out;
}

.member:hover .profile-pic {
  transform: scale(1.1);
}

.social-links {
  display: flex;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-top: 15px;
}

/* Icon LinkedIn */
.social-links .fab.fa-linkedin {
  filter: grayscale(100%); /* Membuat ikon menjadi hitam */
  transition: filter 0.3s ease-in-out; /* Transisi saat hover */
}

.social-links .fab.fa-linkedin:hover {
  filter: none; /* Menghapus filter grayscale saat dihover */
  background-color: #0077b5; /* Warna latar belakang LinkedIn */
  color: #ffffff; /* Warna ikon (putih) saat dihover */
}

/* Icon GitHub */
.social-links .fab.fa-github {
  filter: grayscale(100%); /* Membuat ikon menjadi hitam */
  transition: filter 0.3s ease-in-out; /* Transisi saat hover */
}

.social-links .fab.fa-github:hover {
  filter: none; /* Menghapus filter grayscale saat dihover */
  background-color: #ffffff; /* Warna latar belakang GitHub */
  color: #000000; /* Warna ikon (hitam) saat dihover */
}

/* Icon Instagram */
.social-links .fab.fa-instagram {
  filter: grayscale(100%); /* Membuat ikon menjadi hitam */
  transition: filter 0.3s ease-in-out; /* Transisi saat hover */
}

.social-links .fab.fa-instagram:hover {
  filter: none; /* Menghapus filter grayscale saat dihover */
  background: linear-gradient(to right, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d, #f56040, #f77737, #fcaf45, #ffdc80); /* Warna latar belakang Instagram (gradient warna asli) */
  color: #ffffff; /* Warna ikon (putih) saat dihover */
}

.social-links li {
  margin-right: 10px;
}

.social-links i {
  font-size: 20px; /* Sesuaikan ukuran ikon sesuai kebutuhan */
}

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* DETAIL REVIEW */
.detail-review {
  max-width: 100%;
  font-size: 1em;
  text-align: center;
}

.detail-review-item {
  color: black;
  background-color: lightyellow;
  margin-bottom: 1.5em;
  padding: 1em 1em;
}

.detail-review-item:last-child {
  margin-bottom: 0;
}

.review-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em 1em;
  color: #ed7b7b;
  border-bottom: 1px solid black;
}

.review-name {
  font-weight: bold;
  display: flex;
  align-items: center;
}

.review-date {
  font-size: 0.8em;
  font-weight: lighter;
}

.review-body {
  padding: 1em;
  text-align: left;
}

.mb-3 {
  margin-bottom: 1.3em;
  margin-top: 0.5em;
}

.btn:hover,
.submit-btn:hover {
  color: #fff;
  background-color: #ed7b7b;
}

#submit-review,
.submit-btn {
  background-color: #ed7b7b;
  color: #000000;
  border-color: #ed7b7b;
}

#submit-review:hover {
  background-color: #ed7b7b;
  border-color: #ed7b7b;
}

.btn:active,
.submit-btn:active,
#submit-review:active {
  background-color: #ed7b7b;
  border-color: #ed7b7b;
  color: #fff;
}

.btn-primary .btn-primary:hover {
  background-color: none;
