 font-weight: bold;\n  font-family: \"Quicksand\", serif;\n  margin-bottom: 20px;\n}\n\n.hero h2 {\n  font-family: \"Montserrat\", serif;\n  font-size: 25px;\n  margin-bottom: 20px;\n}\n\n.image-hero {\n  height: 300px;\n  width: 400px;\n  border-radius: 40%;\n  background: #ed7b7b;\n  padding: 9px;\n  overflow: hidden;\n}\n\n.image-hero .img-hero {\n  height: 100%;\n  width: 100%;\n  object-fit: cover;\n  border-radius: 50%;\n}\n\n#navigationDrawer {\n  height: 80px;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  position: fixed;\n  transform: translateY(-730px);\n  transition: transform 0.5s ease-in-out;\n  background: #ef9595;\n  z-index: 99;\n}\n\n#navigationDrawer.open {\n  transform: translateY(-70px);\n}\n\n#navigationDrawer ul {\n  list-style-type: none;\n  padding: 0; /* Remove default padding */\n  margin: 0; /* Remove default margin */\n}\n\n#navigationDrawer a {\n  margin-top: 10px;\n  font-family: \"Quicksand\", serif;\n  text-decoration: none;\n  font-size: 30px;\n  font-weight: bolder;\n  color: #000000;\n  transition: ease-in;\n  transition-duration: 300ms;\n  border-bottom: 1px solid #ffcb0e00;\n  display: block; /* Ensure each link takes full width */\n  align-items: center;\n  padding: 10px; /* Add padding for better touch/click experience */\n}\n\n#navigationDrawer a:hover {\n  color: #fff5e0;\n}\n\n#hamburgerButton {\n  font-size: 32px;\n  color: white;\n  border: none;\n  cursor: pointer;\n  background-color: transparent;\n}\n\n#hamburgerButton:hover {\n  color: #000000;\n}\n\n.content {\n  flex: 1;\n}\n\n.wrapper {\n  display: flex;\n  flex-direction: column;\n  min-height: 100vh;\n}\n\n.blog {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: 150px;\n  gap: 2%;\n  background-color: #ef9595;\n}\n\n.blog > div {\n  width: 100%;\n  font-family: \"Rubik\", sans-serif;\n  font-size: 23px;\n  color: #000000;\n}\n\n.blog > img {\n  object-fit: cover;\n  width: 75%;\n  height: 150px;\n  background-colo